<template>
  <div class="header">
    <div class="header-content">
      <div class="title"></div>
      <h1 class="logo"><router-link :to="{ name: 'wap' }"><img src="../common/images/logo.png" alt=""></router-link></h1>
      <div class="nav">
        <div @click="showNav()" class="nav-button">〓</div>
        <transition name="fade" enter-class="fade-enter" leave-class="fade-leave">
          <div v-show="navShow" @click="hideNav()" class="nav-bg"></div>
        </transition>
        <transition name="move">
          <div v-show="navShow" class="nav-list">
            <div class="nav-head">标题筛选</div>
            <split/>
            <ul>
              <li><router-link :to="{ name: '', params: {} }">1</router-link></li>
              <li><router-link :to="{ name: '', params: {} }">2</router-link></li>
              <li><router-link :to="{ name: '', params: {} }">3</router-link></li>
            </ul>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import split from "./split"

export default {
  name: 'wap',
  data () {
    return {
      msg: '',
      navShow: false
    }
  },
  methods: {
    showNav() {
      this.navShow = true;
    },
    hideNav() {
      this.navShow = false;
    }
  },
  components: {
    split
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped rel="stylesheet/stylus">
.header{
  border-bottom:1px solid #e3e3e3;
  border-top: 3px solid rgb(178, 143, 206);
  height:50px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  background: #fff;
  text-align: center;
  .title {
    height: 50px;
    line-height: 50px;
    font-size: 16px;
  }
  .header-content {
    position: relative;
    .logo {
      position: absolute;
      left: 10px;
      top: 0;
      width: 30px;
      height: 50px;
      line-height: 50px;
      a {
        display:block;
      }
      img{
        width:100%;
        height:auto;
        vertical-align: middle;
      }
    }
    .nav {
      position: absolute;
      right: 0;
      top: 0;
      height:50px;
      line-height: 50px;
      .nav-button {
        width: 50px;
        text-align: center;
      }
      .nav-bg {
        width: 100%;
        height: 100%;
        background-color: rgba(7, 17, 27, 0.5);
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10000;
        transition: all 0.7s;
      }
      .nav-list {
        position: fixed;
        top: 0;
        right: 0;
        z-index: 10001;
        background: rgb(255, 255, 255);
        width: 60%;
        height: 100%;
        border-left: 1px solid #ccc;
        transition: all .7s ease-in
        &.move-enter-active{
          transition-timing-function: cubic-bezier(0, .57, .44, 1.2)
          transform: translate3d(0, 0, 0)
          transition-delay: .1s
        }
        &.move-enter, &.move-leave-active{
          transition-timing-function: ease-in-out
          transform: translate3d(160%, 0, 0)
          transition-delay: .1s
        }
        .nav-head {
          text-align: center;
          height: 42px;
          line-height: 42px;
        }
        li {
          text-align: left;
          a {
            height: 40px;
            line-height: 40px;
            padding-left: 20px;
            border-bottom: 1px solid #e3e3e3;
            display: block;
            color: #333;
          }
        }
      }
    }
  }

  /**
   * 背景渐变
   * @sai
   */
  .fade-transition { opacity: 1; background-color: rgba(7, 17, 27, 0.5); }
	.fade-enter,.fade-leave { opacity: 0; background-color: rgba(7, 17, 27, 0); }
}

</style>



