<template>
  <div class="wap">
    <div class="v-swiperimg">
      <swiper :options="swiperOption" ref="mySwiper" class="">
            <!-- 这部分放你要渲染的那些内容 -->
            <swiper-slide class="swiper-slide">
              <img src="http://www.7wanl.com/data/attachment/portal/201710/16/113447xpddlnsziusigdlz.jpg">
            </swiper-slide>
            <swiper-slide class="swiper-slide">
              <img src="http://www.7wanl.com/data/attachment/portal/201710/16/112949prqri6zmoi5kraky.jpg">
            </swiper-slide>
            <swiper-slide class="swiper-slide">
              <img src="http://www.7wanl.com/data/attachment/portal/201710/16/113019xu7vx357509qux88.jpg">
            </swiper-slide>
            <!-- 这是轮播的小圆点 -->
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
    <ul class="nav">
      <li><router-link :to="{ name: 'Article_list', params: { name: 'name' } }">
        <img src="http://www.7wanl.com/template/7wanl_wap_v1.1/image/touch/nav_wanzi.png">
        <h3>亲子活动</h3>
      </router-link></li>
      <li><router-link :to="{ name: 'Article_list', params: { name: 'name' } }">
        <img src="http://www.7wanl.com/template/7wanl_wap_v1.1/image/touch/nav_wanzi.png">
        <h3>亲子活动</h3>
      </router-link></li>
      <li><router-link :to="{ name: 'Article_list', params: { name: 'name' } }">
        <img src="http://www.7wanl.com/template/7wanl_wap_v1.1/image/touch/nav_wanzi.png">
        <h3>亲子活动</h3>
      </router-link></li>
      <li><router-link :to="{ name: 'Article_list', params: { name: 'name' } }">
        <img src="http://www.7wanl.com/template/7wanl_wap_v1.1/image/touch/nav_wanzi.png">
        <h3>亲子活动</h3>
      </router-link></li>
    </ul>
    <split/>
    <h2 class="title">生活气息</h2>
    <section class="article">
      <div class="article-thumb"><router-link :to="{ name: '', params: {} }">
        <span class="article-thumb-more">阅读更多+</span>
        <img src="http://www.byhill.com/wp-content/uploads/2015/12/image8-370x200.jpg" width="370" height="200" class="article-thumb-img">
      </router-link></div>
      <div class="article-title">
        <p>16 十一月 2015 By admin by <span>大傻逼</span></p>
        <h3><router-link :to="{ name: '', params: {} }">为生活做减法：回归简单的自己</router-link></h3>
      </div>
    </section>
    <section class="article">
      <div class="article-thumb"><router-link :to="{ name: '', params: {} }">
        <span class="article-thumb-more">阅读更多+</span>
        <img src="http://www.byhill.com/wp-content/uploads/2015/12/image8-370x200.jpg" width="370" height="200" class="article-thumb-img">
      </router-link></div>
      <div class="article-title">
        <p>16 十一月 2015 By admin by <span>大傻逼</span></p>
        <h3><router-link :to="{ name: '', params: {} }">为生活做减法：回归简单的自己，为生活做减法：回归简单的自己</router-link></h3>
      </div>
    </section>
    <section class="article">
      <div class="article-thumb"><router-link :to="{ name: '', params: {} }">
        <span class="article-thumb-more">阅读更多+</span>
        <img src="http://www.byhill.com/wp-content/uploads/2015/12/image8-370x200.jpg" width="370" height="200" class="article-thumb-img">
      </router-link></div>
      <div class="article-title">
        <p>16 十一月 2015 By admin by <span>大傻逼</span></p>
        <h3><router-link :to="{ name: '', params: {} }">为生活做减法：回归简单的自己</router-link></h3>
      </div>
    </section>
  </div>
</template>

<script>
import split from "./split"
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  name: 'wap',
  data () {
    return {
      msg: 'sai个人网站',
      swiperOption: {
        //是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
        notNextTick: true,
        pagination: '.swiper-pagination',
        slidesPerView: 'auto',
        paginationClickable: true,
        spaceBetween: 5,
        loop: true,
        onSlideChangeEnd: swiper => {
          //这个位置放swiper的回调方法
          this.page = swiper.realIndex+1;
          this.index = swiper.realIndex;
        }
      }
    }
  },
  //定义这个sweiper对象
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted () {
    //这边就可以使用swiper这个对象去使用swiper官网中的那些方法
    this.swiper.slideTo(0, 0, false);
  },
  components: {
    split,
    swiper,
    swiperSlide
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import '../common/css/swiper.min.css';

.wap {
  .v-swiperimg{
    width: 100%;
    .swiper-slide {
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  .nav {
    width: 100%;
    display: flex;
    li {
      flex: 1;
      text-align:center;
      font-size: 12px;
      margin: 10px 0;
      img {
        width: 40px;
        height: auto;
        margin-bottom: 5px;
      }
    }
  }
  .title {
    margin: 10px 15px;
    padding-top: 3px;
    width: 100%;
    box-sizing: border-box;
    font-weight: normal;
    font-size: 14px;
    color: #999;
    &:before {
        content: " ";
        display: inline-block;
        width: 2px;
        background: #b28fce;
        height: 12px;
        margin-right: 5px;
    }
  }
  .article{
    margin: 10px 20px 30px;
    .article-thumb {
      position: relative;
      width:100%;
      .article-thumb-more {
        position: absolute;
        bottom: 20px;
        left: -8px;
        padding: 9px;
        font-size: 12px;
        color: #fff;
        background-color: rgb(178, 143, 206);
      }
      img {
        width:100%;
        height: auto;
      }
    }
    .article-title {
      p {
        color: rgba(83,83,83,0.7);
        font-size: 12px;
        margin:10px 0;
        font-style: italic;
        span {
          font-style: normal;
          font-size: 13px;
          color: #333;
        }
      }
      h3{
        font-size: 16px;
        line-height: 1.4;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
        a {
          color: #333;
        }
      }
    }
  }
}
</style>



