<template>
  <div class="nav" ref="navwrapper">
      <ul class="nav-list" ref="navlist">
        <router-link :to="{ name: 'Article_list', params: { name: '1' } }"><li>亲自活动1</li></router-link>
        <router-link :to="{ name: 'Article_list', params: { name: '2' } }"><li>慰问费</li></router-link>
        <router-link :to="{ name: 'Article_list', params: { name: '3' } }"><li>阿弥陀佛</li></router-link>
        <router-link :to="{ name: 'Article_list', params: { name: '4' } }"><li>大问题</li></router-link>
        <router-link :to="{ name: 'Article_list', params: { name: '5' } }"><li>恢复期</li></router-link>
        <router-link :to="{ name: 'Article_list', params: { name: '6' } }"><li>微软雅黑</li></router-link>
        <router-link :to="{ name: 'Article_list', params: { name: '7' } }"><li>同人文</li></router-link>
      </ul>
  </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
  name: 'nav',
  data () {
    return {
      msg: 'nav'
    }
  },
  mounted() {
    this.$nextTick(() => {
      this._initNavs();
      this._initScroll();
    })
  },
  methods: {
    _initScroll() {
      if(!this.scroll){
        this.scroll = new BScroll(this.$refs.navwrapper, {
          scrollX: true,
          eventPassthrough: "vertical"
        })
      } else {
        this.scroll.refresh();
      }
    },
    _initNavs() {
      let navlist = this.$refs.navlist;
      let navlistLi = navlist.getElementsByTagName('li');
      let width = 1;
      for(let i = 0; i < navlistLi.length; i++){
        width += Math.ceil(navlistLi[i].offsetWidth);
        // console.log(navlistLi[i].offsetWidth)
      }
      this.$refs.navlist.style.width = width + 'px';
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
.article-scroll {
  position: fixed;
  top: 54px;
  left: 0;
  z-index: 1000;
}
.nav {
  height: 44px;
  line-height: 44px;
  overflow: hidden;
  border-bottom: 1px solid #e3e3e3;
  background-color: #fff;
  font-size: 15px;
  .nav-list {
    li {
      float: left;
      padding: 0 7px;
      height: 44px;
      line-height: 44px;
      a {
        color:#4C4C4C;
      }
    }
  }
}
.router-link-exact-active {
  color: rgb(111, 51, 129);
}
</style>



