<template>
  <div class="article">
    <h2 class="article-title">{{msg}}</h2>
    <p class="article-hook">
      <span class="author">by <a href="javascript:void(0);">小波</a></span>
      <span class="publishtime"><i>2017-08-23 16:45</i></span>
      <span class="category">分类：<a href="javascript:void(0);">情感</a></span>
    </p>
    <div class="article-content">
      <div align="left"><ul><li><strong><font color="#008080"><font face="微软雅黑"><font size="3"> </font></font><font face="微软雅黑"><font size="3">研究结果发现：儿童时期最好的教育莫过于养成良好的习惯。习惯会伴随着我们的成长，要从小养成良好的习惯</font></font></font></strong><br>
      </li></ul></div><div align="left"><div align="left"><font size="3"><br>
      </font></div><div align="left"><font face="微软雅黑"><font size="3">&nbsp; &nbsp;&nbsp; &nbsp;2003年，全国研究生入学考试外语听力成绩首次计入总分，教育主管部门明确规定：“考生必须在13：45前进入考场， 13：45后禁止考生入内。”该规定在考场纪律和考生准考证上均明确写明，各大媒体也都在考前一再提醒。</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　考试当天下午，上海市某大学考点一连来了4名迟到考生，而这时13：55都已过了。当老师告诉他们不得入内时，这几名同学当场傻眼，这才想起看准考证。据说，全国很多考点都有类似情况发生，可以说，任何对自己负责的同学都不应该有这样低级的失误。</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　所以说，其实也是告诫我们每个人都需要养成确认的习惯，例如，平时写完作业认真检查，考试的时候仔细审题，与别人有约的事情要牢记在心并提前落实等。</font></font></div><div align="left"><font face="微软雅黑"><font size="3"> </font></font></div><div align="left"><font face="微软雅黑"><font size="3">现在许多家庭的独身子女在家庭中受到长辈的过分关爱，孩子渐渐养成了懒人模式，</font></font></div><div align="left"><font face="微软雅黑"><font size="3">&nbsp;&nbsp;</font></font></div><div align="left"><font face="微软雅黑"><font size="3">&nbsp; &nbsp;&nbsp; &nbsp; 人的每一阶段的成长都会有不同的关卡， 就如面试而言，看重的并不只是你的学识，也与你自身的行为举止息息相关，而你平常的行为举止直接取决于你日常的行为习惯，所以好的行为习惯要从小开始养成。</font></font></div><br>
      <div align="left"><div align="center"><div align="left"><div align="center">

      <a href="forum.php?mod=viewthread&amp;tid=2679&amp;aid=38561&amp;from=album&amp;page=1&amp;mobile=2" class="orange"><img id="aimg_38561" src="forum.php?mod=image&amp;aid=38561&amp;size=480x480&amp;key=bd66049a2881d70f&amp;type=fixnone" alt="679d2bb1171931f40a7b516d96e7717d63eac7d25c823-3nOqjH_fw658.jpg" title="679d2bb1171931f40a7b516d96e7717d63eac7d25c823-3nOqjH_fw658.jpg"></a>

      </div></div></div></div><br>
      <br>
      <ul><li><font face="微软雅黑"><font size="3"><font color="#008080"><strong>有人会问，习惯到底是什么呢？</strong></font></font></font><br>
      </li></ul><br>
      <div align="left"><font size="3"><font face="微软雅黑">&nbsp; &nbsp;</font><font face="微软雅黑">&nbsp; &nbsp; 习惯就是习以为常的行为，是一种稳定的自动化的行为，是经过反复练习而养成的语言、行为、思维等生活方式，它就是人们头脑中所建立起来的一系列的条件反射。</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　1959年10月，原苏联首位宇航员的选拔工作在全国展开。加加林从3400多名35岁以下的空军飞行员中脱颖而出，并最终成为20名入选者中的唯一一人，在1961年4月12日，乘坐“东方”1号宇宙飞船完成了世界上首次载人宇宙飞行。</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　　为什么加加林能脱颖而出？起决定作用的是一个偶然事件。原来，在确定人选前一个星期，主设计师罗廖夫发现，在进入飞船参观前，只有加加林一个人把鞋脱下来，只穿袜子进入座舱。就是这个细节，一下子赢得了罗廖夫的好感，感动了他。罗廖夫说：“我只有把飞船交给一个如此爱惜它的人，我才放心。”</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　所以，加加林的成功，得益于他良好的习惯。有人开玩笑说：成功从脱鞋开始。好的习惯能够给人带来更多成功的机会，坏的习惯往往使你在不知不觉之中走向失败。在生活中的我们也是，每天看似随便做的一件事极有可能会养成坏的习惯不要忽略每一件细微的小事。</font></font></div><div align="left"><font size="3"><font face="微软雅黑"><br>
      </font></font></div><div align="left"><div align="center"><div align="left"><div align="center">

      <a href="forum.php?mod=viewthread&amp;tid=2679&amp;aid=38557&amp;from=album&amp;page=1&amp;mobile=2" class="orange"><img id="aimg_38557" src="forum.php?mod=image&amp;aid=38557&amp;size=480x480&amp;key=cc38eef79caa1732&amp;type=fixnone" alt="1339b8b6b1545f404fea062163593edbe9b78e0532cdc-mvl4zP_fw658.jpg" title="1339b8b6b1545f404fea062163593edbe9b78e0532cdc-mvl4zP_fw658.jpg"></a>

      </div></div></div></div><br>
      <br>
      <ul><li><font face="微软雅黑"><font size="3"><font color="#008080"><strong>明确行为规范，让孩子对养成某个良好习惯的具体标准清清楚楚。</strong></font></font></font><br>
      </li></ul><br>
      <div align="left"><font size="3"><font face="微软雅黑">&nbsp; &nbsp; </font><font face="微软雅黑">&nbsp; &nbsp;有个新闻曾报道地铁有个小孩子一边吃瓜子一边吐壳在地上 镜头一转原来他的妈妈也这样做…有不少家长会强调对孩子说你现在就养成这习惯长大还得了之类的话，但是其实对孩子影响最深的老师就是父母，父母的一举一动都是对孩子最大的影响，这就是所谓的言传身教。所以要让孩子养成良好习惯的首要就是自身的行为规范</font></font></div><div align="left"><font face="微软雅黑"><font size="3">&nbsp; &nbsp;&nbsp; &nbsp; </font></font></div><div align="left"><font size="3"><font face="微软雅黑">&nbsp; &nbsp;&nbsp; &nbsp; 因为只有细致入微的指导，才能培养出真正的好习惯。甚至可以说，没有细节的指导，就没有儿童教育</font></font><font face="微软雅黑"><font size="3">。</font></font></div><div align="left"><font face="微软雅黑"><font size="3"><br>
      </font></font></div><div align="left"><div align="center"><div align="left"><div align="center">

      <a href="forum.php?mod=viewthread&amp;tid=2679&amp;aid=38558&amp;from=album&amp;page=1&amp;mobile=2" class="orange"><img id="aimg_38558" src="forum.php?mod=image&amp;aid=38558&amp;size=480x480&amp;key=643b9023e30dfddd&amp;type=fixnone" alt="43c50c7b63de869a7b5b5f7d47e763d2215de0681ce9c-HPxloJ_fw658.jpg" title="43c50c7b63de869a7b5b5f7d47e763d2215de0681ce9c-HPxloJ_fw658.jpg"></a>

      </div></div></div></div><br>
      <br>
      <ul><li><font face="微软雅黑"><font size="3"><font color="#008080"><strong>适时进行榜样教育让孩子对养成某个良好习惯产生亲切而向往的感情。</strong></font></font></font><br>
      </li></ul><br>
      <div align="left">　　&nbsp;&nbsp;<font size="3"><font face="微软雅黑">天津社科院的关颖研究员曾与儿子难以沟通，但儿子房间里贴了许多篮球明星乔丹的画像，因为乔丹是他心中的偶像。</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　后来，一向不喜欢篮球的关颖，开始与儿子一起看篮球比赛了，并且收集了许多乔丹的故事，这让儿子对妈妈刮目相看，自然亲近了起来。关颖发现，只要与儿子谈乔丹，儿子就心服口服，因为他对这个偶像有认同感、亲切感。</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　&nbsp; &nbsp;结果，借助乔丹这个榜样的力量，全家人和谐相处，儿子也越来越可爱。</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　</font></font></div><div align="left"><font face="微软雅黑"><font size="3">　&nbsp;&nbsp;儿童时代就是榜样时代和偶像时代，因为儿童的学习特点就是观察和模仿。父母和教师既可以选择孩子喜爱的名人榜样，也可以选择孩子的优秀伙伴，只要发现他们的某些好习惯，都会对孩子产生巨大的影响力。</font></font></div><div align="left"><font face="微软雅黑"><font size="3"><br>
      </font></font></div><div align="left"><div align="center"><div align="left"><div align="center">

      <a href="forum.php?mod=viewthread&amp;tid=2679&amp;aid=38559&amp;from=album&amp;page=1&amp;mobile=2" class="orange"><img id="aimg_38559" src="forum.php?mod=image&amp;aid=38559&amp;size=480x480&amp;key=c8ecf58c2b9cc725&amp;type=fixnone" alt="4c946055f845a2a9a4e098f983c7f2122bb3b856262735-WZG2HP_fw658.jpg" title="4c946055f845a2a9a4e098f983c7f2122bb3b856262735-WZG2HP_fw658.jpg"></a>

      </div></div></div></div><br>
      <br>
      <ul><li><font face="微软雅黑"><font size="3"><font color="#008080"><strong>形成良好的环境或风气，让家庭生活和学校环境乃至社会风气成为孩子养成良好习惯的支持力量。</strong></font></font></font><br>
      </li></ul><br>
      <div align="left"><font size="3"><font face="微软雅黑">　　环境的力量很大，培养孩子好习惯一定要注意形成一个良好的环境。比如当孩子在学习的时候，有的父母边哗啦哗啦打麻将边说：“儿子，好好学习啊，考北大、考清华。”作用是相互的习惯的养成，父母与孩子的联系息息相关的，培养好的习惯要注意形成一个良好的环境。</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　习惯的培养是门科学。那么习惯培养的原则是什么呢？是要尊重孩子，尊重孩子的主人地位。习惯培养的目标是什么呢？是培养良好习惯来解放孩子的大脑，让孩子从一些低级的、束缚自己的不良行为习惯中解放出来。比方不磨蹭，不言而无信，不做一些低级趣味的事情，要使孩子生活得很有情趣、很有意义。</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　习惯培养的过程是两代人相互学习、共同成长的过程。有些习惯孩子比大人形成的早，像环保，我们就要向孩子学习。父母和孩子一起成长。而且好的关系胜过许多教育，父母老师跟孩子的关系越好越有助于孩子良好习惯的形成，这样才有亲和力，亲其师信其道。</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　</font></font></div><div align="left"><font size="3"><font face="微软雅黑">　　习惯决定孩子命运，教育的核心是培养健康人格，培养健康人格最有效的途径就是从培养行为习惯做起。我们抓住行为习惯培养这个根本，就抓住了家庭教育最有效的一条途径，这就是我们家庭教育最基本的任务。</font></font></div><div align="left"><font size="3"><font face="微软雅黑"><br>
      </font></font></div><div align="left"><div align="center"><div align="left"><div align="center">

      <a href="forum.php?mod=viewthread&amp;tid=2679&amp;aid=38560&amp;from=album&amp;page=1&amp;mobile=2" class="orange"><img id="aimg_38560" src="forum.php?mod=image&amp;aid=38560&amp;size=480x480&amp;key=6d8579d7fa27d1f0&amp;type=fixnone" alt="04e2363e271b6a06d699920f7056f689966d53a712ee81-TikyhG_fw658.jpg" title="04e2363e271b6a06d699920f7056f689966d53a712ee81-TikyhG_fw658.jpg"></a>

      </div></div></div></div></div>
    </div>
