<template>
  <div class="artlist">
    <v-nav :class=" {'article-scroll': articleScrollY }" />
    <div class="for-nav" v-show="articleScrollY"></div>
    <div class="loading" v-if="loading">Loading...</div>
    <div class="article-list" v-else ref="articleWrapper" >
      <section class="article">
        <div class="article-thumb"><router-link :to="{ name: 'Article_view', params: { id: '1' } }">
          <span class="article-thumb-more">阅读更多+</span>
          <img src="http://www.byhill.com/wp-content/uploads/2015/12/image8-370x200.jpg" width="370" height="200" class="article-thumb-img">
        </router-link></div>
        <div class="article-title">
          <p>16 十一月 2015 By admin by <span>大傻逼</span></p>
          <h3><router-link :to="{ name: 'Article_view', params: { id: '1' } }">为生活做减法：回归简单的自己，为生活做减法：回归简单的自己</router-link></h3>
        </div>
      </section>
      <section class="article">
        <div class="article-thumb"><router-link :to="{ name: 'Article_view', params: { id: '2' } }">
          <span class="article-thumb-more">阅读更多+</span>
          <img src="http://www.byhill.com/wp-content/uploads/2015/12/image8-370x200.jpg" width="370" height="200" class="article-thumb-img">
        </router-link></div>
        <div class="article-title">
          <p>16 十一月 2015 By admin by <span>大傻逼</span></p>
          <h3><router-link :to="{ name: 'Article_view', params: { id: '2' } }">为生活做减法：回归简单的自己，为生活做减法：回归简单的自己</router-link></h3>
        </div>
      </section>
      <section class="article">
        <div class="article-thumb"><router-link :to="{ name: 'Article_view', params: { id: '3' } }">
          <span class="article-thumb-more">阅读更多+</span>
          <img src="http://www.byhill.com/wp-content/uploads/2015/12/image8-370x200.jpg" width="370" height="200" class="article-thumb-img">
        </router-link></div>
        <div class="article-title">
          <p>16 十一月 2015 By admin by <span>大傻逼</span></p>
          <h3><router-link :to="{ name: 'Article_view', params: { id: '3' } }">为生活做减法：回归简单的自己，为生活做减法：回归简单的自己</router-link></h3>
        </div>
      </section>
      <section class="article">
        <div class="article-thumb"><router-link :to="{ name: 'Article_view', params: { id: '4' } }">
          <span class="article-thumb-more">阅读更多+</span>
          <img src="http://www.byhill.com/wp-content/uploads/2015/12/image8-370x200.jpg" width="370" height="200" class="article-thumb-img">
        </router-link></div>
        <div class="article-title">
          <p>16 十一月 2015 By admin by <span>大傻逼</span></p>
          <h3><router-link :to="{ name: 'Article_view', params: { id: '4' } }">为生活做减法：回归简单的自己，为生活做减法：回归简单的自己</router-link></h3>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import vNav from '@/components/nav'

export default {
  name: 'Article_list',
  data () {
    return {
      loading: false,
      scrollY: 0,
      articleScrollY: false
    }
  },
  computed: {

  },
  mounted() {
    this.$nextTick(() => {

    })
    window.addEventListener('scroll', this._articleScroll)
  },
  methods: {
    _articleScroll () {
      let t = document.documentElement.scrollTop || document.body.scrollTop;
      if( t >= 45 ){
        this.articleScrollY = true;
      } else if( t == 0) {
        this.articleScrollY = false;
      }
    }
  },
  components: {
    vNav
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
// 防止 nav 在定位过程出现弹动
.for-nav {
  height: 45px;
  width: 100%;
}
.article{
  margin: 10px 20px 30px;
  .article-thumb {
    position: relative;
    width:100%;
    .article-thumb-more {
      position: absolute;
      bottom: 20px;
      left: -8px;
      padding: 9px;
      font-size: 12px;
      color: #fff;
      background-color: rgb(178, 143, 206);
    }
    img {
      width:100%;
      height: auto;
    }
  }
  .article-title {
    p {
      color: rgba(83,83,83,0.7);
      font-size: 12px;
      margin:10px 0;
      font-style: italic;
      span {
        font-style: normal;
        font-size: 13px;
        color: rgb(111, 51, 129);
      }
    }
    h3{
      font-size: 16px;
      line-height: 1.4;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space:nowrap;
      a {
        color: rgb(60, 47, 65);
      }
    }
  }
}
</style>



