<template>
  <div id="app">
    <v-header/>
    <keep-alive><router-view class="router-view"/></keep-alive>
    <v-footer/>
  </div>
</template>

<script>
import vHeader from './components/header'
import vFooter from './components/footer'

export default {
  name: 'app',
  components: {
    vHeader,
    vFooter
  }
}
</script>

<style>
body, html { height: 100%; }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  position: relative;
  height: 100%;
}
/**
 * sticky footer
 * @sai
 *
 * footer {
 *  padding: 10px 0;
 *  margin-top:-85px;
 *  height: 85px;
 *  box-sizing: border-box;
 * }
 */
.router-view {
  padding-top: 55px;
  padding-bottom: 85px;
  min-height: 100%;
  box-sizing: border-box;
  overflow:hidden;
}
</style>



